import React, { useEffect } from 'react';
import './App.css';
import MainScreen from './components/Main';

function App() {
  useEffect(() => {
    const tg = (window as any).Telegram?.WebApp;
    if (!tg) return;

    tg.ready(); //  Telegram,   

    //      ( -  )
    try {
      tg.MainButton.hide();
      //       
      // (   )
      tg.MainButton.offClick?.(() => {});
    } catch (_) {
      /* ignore */
    }
  }, []);

  return (
    <div className="App">
      <MainScreen />
    </div>
  );
}

export default App;
